/*Reset*/
*{margin:0; padding:0;font-weight: 300;border:none;outline:none;box-sizing: border-box;font-family: "Arial";overflow:auto;}
html,body{
  height:100%;
  width:100%;
  overflow:hidden;
  margin:'0';}
#conteudo{
  background-image: url(/devalanreis/static/media/BackgroundGrande.695c9fd3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  align-content: center;
  height:100vh ;}
.icone{
  right: 50px;
  top: 5%;
  position: absolute;
  width: 80px;
  height: auto;}
#Menu-Principal{
  height: 100vh;  }
#Menu-Nav a:hover{
    text-decoration: underline;
    font-weight: '500';
    color:'blue'!important;}
 .secao{
  height: 100%;
  width: 100%;
  overflow: hidden;}
.botao{
  width: 40px;
    height: 40px;
    right: 50px;
    top: 85%;
    background-color: transparent;
    outline: 0 !important;
  border: none !important;}

    .btn:focus{
      outline-style: none !important;
      outline:0 !important;
      box-shadow: none !important;
      border:none !important;
      }
.primeiro-botao{
    right: 100px;
    -webkit-transform:rotate(180deg);
            transform:rotate(180deg);}
.botao-fechar{
  background-color:#ff2400;
  }
.imagem-botao{
  object-fit:fill;
  height:90%;
  width:90%;
  margin:auto;
}
.bgApresentacao {
background: -webkit-linear-gradient(334.96deg, #000000  -16.85% , rgba(0,0,0,0.6)  103.44%,black  ) ;
background: linear-gradient(115.04deg, #000000  -16.85% , rgba(0,0,0,0.6)  103.44%,black  ) ;}
.bgHabilidades {
  background: -webkit-linear-gradient(334.96deg , #FF0000  -16.85% , rgba(255,0,0,0.6) 103.44%, #FF0000  ) ;
  background: linear-gradient(115.04deg , #FF0000  -16.85% , rgba(255,0,0,0.6) 103.44%, #FF0000  ) ; }
.bgObjetivo {
    background: -webkit-linear-gradient(334.96deg , #4169e1  -16.85% , rgba(65,105,225,0.5) 103.44% ,#4169e1   ) ;
    background: linear-gradient(115.04deg , #4169e1  -16.85% , rgba(65,105,225,0.5) 103.44% ,#4169e1   ) ;   }
.bgContato {
    background: -webkit-linear-gradient(355deg, #0094FF  0% , rgba(0,157,255,0.8) 45% ,#0094FF ) ;
    background: linear-gradient(95deg, #0094FF  0% , rgba(0,157,255,0.8) 45% ,#0094FF ) ;  }
.bgContato::-webkit-scrollbar{
        width: 1em;    }
.bgContato::-webkit-scrollbar-track {
      background-color:white;    }
.bgContato::-webkit-scrollbar-thumb {
      background-color: #32a9ff;
      border-radius:2px;}
.bgHabilidades::-webkit-scrollbar{
         width: 1em;  }
.bgHabilidades::-webkit-scrollbar-track {
    background-color:white;}   
.bgHabilidades::-webkit-scrollbar-thumb {
    background-color: #ff5b5b;
    border-radius:2px;}  
.bgApresentacao::-webkit-scrollbar{
    width: 1em;}
.bgApresentacao::-webkit-scrollbar-track {
  background-color:white;}
.bgApresentacao::-webkit-scrollbar-thumb {
  background-color: dimgray;
  border-radius:2px;} 
.bgObjetivo::-webkit-scrollbar{
  width: 1em;}
.bgObjetivo::-webkit-scrollbar-track {
background-color:white;}
.bgObjetivo::-webkit-scrollbar-thumb {
background-color: #8da5ed;
border-radius:2px;}
#Menu-Nav{
        -webkit-column-count: 4;
                column-count: 4;
        -webkit-column-width: 25%;
                column-width: 25%;}
.barra-contato::before{
  width: 0;
height: 0;
border: 0 solid transparent;
border-right-width: 25px;
border-left-width: 0px;
border-bottom: 15px solid white;
content: '';
position:absolute;
-webkit-transform: rotate(90deg) translateY(50%) translateX(216%);
        transform: rotate(90deg) translateY(50%) translateX(216%);
left:0;
margin:0;
padding: 0;



}
